.main {
  padding: 2em 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.375em;
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: 50%;
    max-width: 40.625em;
  }
  &__img {
    position: relative;
    width: 50%;
    flex-grow: 1;
    margin-right: -6.25em;
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }
  &__subtitle {
    position: relative;
    max-width: 37.875em;
    padding: 0 0 0 1.25em;
    margin-bottom: 1.5em;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 0.125em;
      height: 100%;
      background: #03045E;
      border-radius: 0.188em;
    }
    span {
      font-size: 1.25em;
      line-height: 115%;
      color: #36317B;
    }
  }
  &__title {
    max-width: 15em;
  }
  &__nav {
    margin-top: 2em;
  }
  &__subtitle2 {
    font-size: 1.25em;
    line-height: 115%;
    font-weight: 600;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75em;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 1.25em;
    background: #F6F7FB;
    border-radius: 0.75em;
    padding: 0.75em 1.25em;
    &-icon {
      width: 1.25em;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 1em;
      line-height: 115%;
    }
    strong {
      display: block;
      margin-bottom: 0.5em;
    }
  }
  &__btn {
    height: 3.313em;
    padding: 0.313em 1.5em 0.313em 1.5em;
    margin-top: 2em;
    span {
      font-size: 1.125em;
    }
    .btn-logo {
      width: 102px;
      img {
        width: 100%;
      }
    }
  }
  &__btn--mob {
    display: none;
  }
  .circle {
    position: absolute;
    top: -9.375em;
    right: -7.5em;
    width: 15.25em;
    height: 15.25em;
    background: #0BAE5F;
    filter: blur(11.688em);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    left: 2.5em;
    bottom: -5em;
    width: 12.5em;
    height: 12.5em;
    background: #0858F4;
    filter: blur(8.188em);
    border-radius: 50%;
    pointer-events: none;
  }
}
.banner {
  position: relative;
  z-index: 2;
  padding: 2em 0 2.813em;
  &__inner {
    position: relative;
    background: #000;
    border-radius: 1em;
    padding: 3.125em;
    overflow: hidden;
  }
  &__text {
    position: relative;
    z-index: 3;
    max-width: 47.25em;
    p {
      font-size: 1.375em;
      line-height: 115%;
      font-weight: 600;
      color: #fff;
    }
    .title-logo {
      position: relative;
      width: 7.5em;
      display: inline-flex;
      img {
        position: absolute;
        left: 0;
        bottom: -0.438em;
        width: 100%;
      }
    }
    a {
      text-decoration: underline;
      color: #0BAE5F;
      &:hover {
        text-decoration: unset;
      }
    }
  }
  &__img {
    position: absolute;
    z-index: 2;
    right: 1em;
    bottom: 0;
    width: 100%;
    max-width: 26em;
    img {
      width: 100%;
      display: block;
    }
  }
  .circle {
    position: absolute;
    top: -1.188em;
    right: -4.125em;
    width: 7.25em;
    height: 7.25em;
    background: #3ED05E;
    filter: blur(8em);
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    top: -6.625em;
    right: 2.375em;
    width: 11.063em;
    height: 11.063em;
    background: #3ED05E;
    filter: blur(6em);
    opacity: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    top: -5.125em;
    right: 16.25em;
    width: 11.063em;
    height: 11.063em;
    background: #3ED05E;
    filter: blur(6em);
    opacity: 50%;
    pointer-events: none;
  }
  .circle4 {
    position: absolute;
    right: 0.938em;
    bottom: -13.125em;
    width: 17.375em;
    height: 17.375em;
    background: #0858F4;
    filter: blur(6em);
    opacity: 50%;
    pointer-events: none;
  }
}
.advantages {
  padding: 2.813em 0;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    border: 0.063em solid #E2E9F4;
    border-radius: 2em;
    margin-top: 3.75em;
  }
  &__item {
    display: flex;
    align-items: stretch;
    &:first-child {
      .advantages__item-img {
        border-radius: 0 2em 0 0;
      }
      .advantages__item-desc {
        border-radius: 2em 0 0 0;
      }
    }
    &:last-child {
      .advantages__item-img {
        border-radius: 0 0 2em 0;
      }
      .advantages__item-desc {
        border-radius: 0 0 0 2em;
      }
    }
    &:nth-child(odd) {
      .advantages__item-img {
        order: 1;
      }
      .advantages__item-desc {
        background: #F5F9FF;
      }
    }
    &:nth-child(even) {
      .advantages__item-desc {
        background: #fff;
      }
    }
    &-img {
      position: relative;
      width: 26.875em;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F5F7FB;
      padding: 1em;
      overflow: hidden;
      &:before {
        content: '';
        position: absolute;
        left: -5.75em;
        bottom: -5.75em;
        width: 11.5em;
        height: 11.5em;
        background: rgba(48, 106, 255, 0.26);
        filter: blur(4em);
        border-radius: 50%;
        pointer-events: none;
      }
      &:after {
        content: '';
        position: absolute;
        top: -5.75em;
        right: -5.75em;
        width: 11.5em;
        height: 11.5em;
        background: rgba(48, 106, 255, 0.26);
        filter: blur(4em);
        border-radius: 50%;
        pointer-events: none;
      }
      img {
        max-width: 70%;
        max-height: 100%;
      }
    }
    &-desc {
      width: calc(100% - 26.875em);
      padding: 3.125em 2.5em;
    }
    &-text {
      margin-top: 1.25em;
      p {
        font-size: 1.125em;
        line-height: 115%;
        color: #555B71;
      }
    }
  }
}
.info {
  &__inner {
    border-bottom: 0.063em solid #555B71;
    padding: 2.813em 0 4em;
  }
  &__wrap {
    display: flex;
    align-items: center;
    gap: 1.5em;
    margin-bottom: 1.5em;
  }
  &__group {
    width: 16.313em;
    flex-shrink: 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.5em / 2));
    gap: 1.5em;
  }
  &__item {
    background: #F5F7FB;
    border-radius: 1em;
    padding: 2.5em 1.5em 1.5em 1.5em;
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 1em;
    }
    &-title {
      font-size: 1.25em;
      line-height: 115%;
      font-weight: 600;
    }
    &-number {
      flex-shrink: 0;
      font-size: 1.75em;
      line-height: 100%;
      font-weight: 700;
      color: #03045E;
    }
    &-text {
      margin-top: 1.5em;
      p {
        font-size: 1.125em;
        line-height: 115%;
        color: #555B71;
      }
    }
  }
}
.analysis {
  padding: 4em 0;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.5em / 2));
    gap: 1.5em;
    margin-top: 2.5em;
  }
  &__item {
    border: 0.063em solid #DBDCDE;
    border-radius: 1em;
    padding: 2.5em;
    &-flex {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 0.875em;
    }
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
    gap: 2em;
  }
  &__subitem {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
  &__text {
    font-size: 1.125em;
    line-height: 115%;
    font-weight: 600;
  }
  &__text2 {
    font-size: 1.5em;
    line-height: 115%;
    font-weight: 600;
    color: #0BAE5F;
    span {
      display: block;
    }
  }
  &__group {
    position: relative;
    width: 15.625em;
    flex-shrink: 0;
  }
  &__img {
    img {
      width: 100%;
      border-radius: 1em;
    }
  }
  &__btn {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 1.25em;
    transform: translate(-50%, 0);
    width: calc(100% - 2.5em);
    height: 2.75em;
    padding: 0.313em 0.625em;
    &:hover {
      i svg path {
        fill: #03045E;
      }
    }
    i {
      width: 1.438em;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
        transition: all 0.3s;
      }
    }
    span {
      font-size: 0.875em;
      line-height: 115%;
      font-weight: 500;
    }
  }
}
.stages {
  background: #F5F7FB;
  padding: 4.375em 0;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 1.25em / 3));
    gap: 0.625em;
    margin-top: 2.5em;
  }
  &__btn {
    height: 3.313em;
    padding: 0.313em 1.5em;
    margin: 2.5em auto 0;
    span {
      font-size: 1.125em;
    }
  }
}
.stage-card {
  position: relative;
  background: #fff;
  border: 1px solid #DBDCDE;
  border-radius: 1em;
  padding: 40px;
  &__number {
    position: relative;
    z-index: 2;
    width: 2.875em;
    height: 2.875em;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #03045E;
    border-radius: 50%;
    span {
      font-size: 1em;
      line-height: 115%;
      font-weight: 500;
      color: #36317B;
    }
  }
  &__img {
    position: absolute;
    top: 1em;
    right: 1em;
    max-width: 8.125em;
    pointer-events: none;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    margin-top: 2.5em;
  }
  &__title {
    font-size: 1.125em;
    line-height: 115%;
    font-weight: 600;
  }
  &__text {
    margin-top: 0.625em;
    p {
      font-size: 1.125em;
      line-height: 115%;
    }
  }
}
.tariffs {
  padding: 4em 0;
  &__top {
    max-width: 64.188em;
    text-align: center;
    margin: 0 auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
    gap: 1.875em;
    margin-top: 3.75em;
  }
  .scroll-banner {
    width: 0;
    height: 0;
    display: block;
    opacity: 0;
    pointer-events: none;
  }
}
.tariff-card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 2.125em;
  &__wrap {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background: #F5F7FB;
    border: 0.063em solid #DBDCDE;
    border-radius: 1em;
    padding: 1.5em;
  }
  &__icon {
    position: absolute;
    top: 0;
    right: 3.5em;
    width: 2.375em;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 1.25em;
    line-height: 115%;
    font-weight: 600;
  }
  &__text {
    margin-top: 2em;
    margin-bottom: auto;
    p {
      font-size: 0.875em;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
    }
  }
  &__price-wrap {
    position: relative;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75em;
    padding: 1em 0;
    margin-top: 1.5em;
    &:after {
      content: '';
      position: absolute;
      z-index: 0;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% + 3em);
      height: 100%;
      background: #03045E;
      pointer-events: none;
    }
  }
  &__price-title {
    position: relative;
    z-index: 2;
    font-size: 0.75em;
    line-height: 115%;
    font-weight: 500;
    color: #fff;
  }
  &__price {
    position: relative;
    z-index: 2;
    font-size: 1.125em;
    line-height: 115%;
    font-weight: 600;
    color: #3ED05E;
  }
  &__btn {
    width: 100%;
    height: 2.875em;
    padding: 0.313em 0.625em;
    margin-top: 1.5em;
    span {
      font-size: 1em;
    }
  }
  &__info {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 100%;
    text-align: center;
    p {
      font-size: 0.75em;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
    }
  }
}
.tariffs-banner {
  margin-top: 4em;
  &--active {
    .tariffs-banner__btn {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
    .tariffs-banner__hidden {
      display: block;
    }
    .tariffs-banner__btn2 {
      display: flex;
    }
  }
  &__wrap {
    background: #F5F7FB;
    border: 0.063em solid #EAECF1;
    border-radius: 1em;
  }
  &__show {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2em;
    padding: 1.25em 2em;
    overflow: hidden;
  }
  &__title {
    position: relative;
    z-index: 2;
    font-size: 1.25em;
    line-height: 115%;
    font-weight: 600;
  }
  &__group {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    gap: 2em;
  }
  &__price {
    font-size: 1.125em;
    line-height: 115%;
    font-weight: 600;
    color: #0BAE5F;
  }
  &__btn {
    height: 2.875em;
    padding: 0.313em 2.875em;
    span {
      font-size: 1em;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  .circle {
    position: absolute;
    top: -1.5em;
    right: -1.5em;
    width: 5.125em;
    height: 5.125em;
    background: #3ED05E;
    filter: blur(4em);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    top: -3em;
    right: 11.125em;
    width: 5.125em;
    height: 5.125em;
    background: #3ED05E;
    filter: blur(4em);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    right: 9.063em;
    bottom: -2em;
    width: 5.125em;
    height: 5.125em;
    background: #3ED05E;
    filter: blur(4em);
    border-radius: 50%;
    pointer-events: none;
  }
  &__hidden {
    display: none;
  }
  &__text {
    p {
      border-top: 0.063em solid #EAECF1;
      padding: 1.5em 2em;
      font-size: 1em;
      line-height: 115%;
      color: #555B71;
    }
  }
  &__btn2 {
    height: 2.875em;
    display: none;
    padding: 0.313em 1.5em;
    margin: 2em auto 0 auto;
    span {
      font-size: 1.125em;
    }
  }
}
