.case-card {
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 0.0623em solid #DBDCDE;
  border-radius: 1em;
  padding: 1.875em;
  &__group {
    border-top: 0.0623em solid rgba(85, 91, 113, 0.3);
    padding: 1.5em 0;
    &:first-child {
      border: none;
      padding-top: 0;
    }
    &2 {
      position: relative;
      min-height: 11.125em;
      background: #000;
      border-radius: 0.625em;
      padding: 1.25em;
      color: #fff;
      margin-top: auto;
      overflow: hidden;
      .case-card__title {
        color: #3ED05E;
      }
      .case-card__list {
        li {
          color: #fff;
          &:after {
            background: #fff;
          }
        }
      }
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    font-size: 1.125em;
    line-height: 115%;
    font-weight: 600;
  }
  &__list {
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.313em;
    margin-top: 1.25em;
    li {
      position: relative;
      padding-left: 0.625em;
      font-size: 0.875em;
      line-height: 115%;
      color: #555B71;
      &:after {
        content: '';
        position: absolute;
        top: 0.438em;
        left: 0;
        width: 0.125em;
        height: 0.125em;
        background: #555B71;
        border-radius: 50%;
      }
    }
  }
}
.banner2 {
  padding: 4em 0 2.813em;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2em;
    background: #000;
    border-radius: 1em;
    padding: 2.5em;
    overflow: hidden;
  }
  &__desc {
    position: relative;
    z-index: 2;
    max-width: 36.875em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #fff;
  }
  &__title {
    position: relative;
    z-index: 2;
    font-size: 2em;
    line-height: 115%;
    font-weight: 600;
  }
  &__text {
    position: relative;
    z-index: 2;
    margin-top: 1.5em;
    p {
      font-size: 1.125em;
      line-height: 115%;
      font-weight: 500;
