.questions {
  padding: 2.813em 0 5.625em;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 2.5em;
  }
  &__group {
    width: 38%;
  }
  &__img {
    position: relative;
    max-width: 30em;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 5em;
      height: 5em;
      background: #0BAE5F;
      filter: blur(5em);
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 100%;
    }
  }
  &__title {
    margin-top: 0.625em;
  }
  &__items {
    width: 62%;
    height: 22em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    padding-right: 1.875em;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 0.25em;
    }
    &::-webkit-scrollbar-track {
      background: #F5F7FB;
      border-radius: 0.188em;
    }
    &::-webkit-scrollbar-thumb {
      background: #36317B;
      border-radius: 0.188em;
    }
  }
  &__item {
    background: #F5F7FB;
    border-radius: 1em;
    padding: 1em;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 0.5em;
      cursor: pointer;
    }
    &-title {
      font-size: 1.125em;
      line-height: 115%;
      font-weight: 600;
    }
    &-btn {
      width: 2.75em;
      height: 2.75em;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 0.063em solid #36317B;
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        max-width: 80%;
        max-height: 80%;
      }
    }
    &-hidden {
      display: none;
      margin-top: 1em;
    }
    &-text {
      p {
        font-size: 0.875em;
        line-height: 115%;
        color: #555B71;
      }
      ul {
        li {
          position: relative;
          padding-left: 0.625em;
          font-size: 0.875em;
          line-height: 115%;
          color: #555B71;
          &:after {
            content: "";
            position: absolute;
            top: 0.438em;
            left: 0;
            width: 0.125em;
            height: 0.125em;
            background: #555b71;
            border-radius: 50%;
          }
        }
      }
    }
  }
  .item-show--active {
    .questions__item-title {
      color: #03045E;
    }
    .questions__item-btn {
      transform: rotate(-180deg);
      background: #03045E;
      svg path {
        fill: #fff;
      }
    }
  }
}

