.wrapper {
  overflow: hidden;
}
.btn-blue__outline {
  background: #fff;
  border: 0.063em solid #36317B;
  border-radius: 100px;
  color: #36317B;
  &:hover {
    background: #36317B;
    color: #fff;
  }
  span {
    line-height: 115%;
    font-weight: 500;
  }
}
.btn-blue {
  gap: 0.875em;
  background: #36317B;
  border: 0.063em solid transparent;
  border-radius: 6.25em;
  color: #fff;
  &:hover {
    background: #fff;
    border-color: #36317B;
    color: #36317B;
  }
  span {
    line-height: 115%;
    font-weight: 500;
  }
  .btn-logo {
    display: flex;
    align-items: center;
  }
}
.h2 {
  font-size: 2.5em;
  line-height: 115%;
  font-weight: 700;
  .title-logo {
    position: relative;
    width: 4.15em;
    min-height: 1em;
    display: inline-flex;
    img {
      position: absolute;
      left: 0;
      bottom: -0.15em;
      width: 100%;
    }
  }
}
.h3 {
  font-size: 1.75em;
  line-height: 115%;
  font-weight: 700;
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  width: 2.875em;
  height: 2.875em;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 0.0623em solid #36317B;
  border-radius: 50%;
  &:hover {
    background: #36317B;
    svg path {
      fill: #fff;
    }
  }
  &:after {
    display: none;
  }
  svg {
    max-width: 80%;
    max-height: 80%;
    min-width: 0.5em;
    height: auto;
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    background: #03045E !important;
    opacity: 0.3;
    &-active {
      opacity: 1;
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(1, 0, 30, 0.6);
  backdrop-filter: blur(10px);
  padding: 0 0.938em;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 3.125em auto;
    }
  }
  &__close {
    position: absolute;
    top: 1.5em;
    right: 1.5em;
    width: 1.25em;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #111;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__icon {
    max-width: 12.188em;
    img {
      width: 100%;
    }
  }
  &__info {
    font-size: 1em;
    line-height: 115%;
    font-weight: 600;
    color: #555B71;
  }
  &__close2 {
    height: 2.75em;
    padding: 0.313em 0.625em;
    span {
      font-size: 0.875em;
    }
  }

}
.modal-form {
  .modal__dialog {
    max-width: 38.125em;
    background: #fff;
    border-radius: 1em;
    padding: 1.875em 1.25em;
  }
  .modal__title {
    text-align: center;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 2em;
    &-group {
      input {
        width: 100%;
        height: 2.75em;
        border: 0.063em solid #EDEDED;
        border-radius: 2.375em;
        padding: 0 1em;
        font-size: 1em;
        line-height: 115%;
        font-weight: 500;
        color: #111;
        &::placeholder {
          color: #555B71;
        }
      }
      textarea {
        width: 100%;
        height: 7.5em;
        border: 0.063em solid #EDEDED;
        border-radius: 1em;
        padding: 1em;
        resize: none;
        font-size: 1em;
        line-height: 115%;
        font-weight: 500;
        color: #111;
        &::placeholder {
          color: #555B71;
        }
      }
    }
    &-text {
      p {
        font-size: 0.875em;
        line-height: 115%;
      }
    }
    &-flex {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      gap: 1em;
    }
    .file {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
      }
      label {
        width: 100%;
        height: 2.75em;
        gap: 0.625em;
      }
      &-icon {
        width: 1.125em;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
      &-text {
        font-size: 0.875em;
      }
    }
    &-check {
      text-align: center;
      font-size: 0.75em;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
      a {
        position: relative;
        z-index: 2;
      }
      a:hover {
        color: #03045E;
      }
    }
    &-btn {
      width: 100%;
      height: 2.75em;
      span {
        font-size: 0.875em;
      }
    }
  }
}
.modal-form2 {
  .modal__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .modal__title {
    margin-top: 3.125em;
  }
  .modal__info {
    margin-top: 1.5em;
  }
  .modal__close2 {
    width: 100%;
    margin-top: 3.125em;
  }
}


@media screen and (max-width: 992px) {
  .h2 {
    font-size: 1.75em;
    .title-logo {
      width: 5em;
      img {
        bottom: -0.25em;
      }
    }
  }
  .h3 {
    font-size: 1.25em;
  }
  .modal {
    &__icon {
      max-width: 9.375em;
    }
    &__info {
      font-size: 0.875em;
    }
  }
  .modal-form {
    .modal__dialog {
      padding: 1.25em;
    }
    .form {
      &-flex {
        grid-template-columns: 100%;
      }
    }
  }
  .modal-form2 {
    .modal__title {
      margin-top: 2em;
    }
    .modal__info {
      margin-top: 1.25em;
    }
    .modal__close2 {
      margin-top: 1.25em;
    }
  }
}