.footer {
  position: relative;
  background: #000;
  border-radius: 1em 1em 0 0;
  color: #fff;
  overflow: hidden;
  &__top {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
    padding: 1.875em 0;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 3.125em;
  }
  &__logo {
    width: 8.5em;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 1em;
    li {
      font-size: 0.875em;
      line-height: 115%;
      font-weight: 500;
    }
    li a {
      &:hover {
        color: #36317B;
      }
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 3.125em;
  }
  &__btn {
    height: 2.5em;
    background: transparent;
    border: 0.063em solid #fff;
    border-radius: 100px;
    padding: 0.313em 1.25em;
    color: #fff;
    &:hover {
      background: #fff;
      color: #111;
    }
    span {
      font-size: 0.875em;
      line-height: 115%;
      font-weight: 500;
    }
  }
  &__bottom {
    position: relative;
    z-index: 2;
    border-top: 0.063em solid rgba(255, 255, 255, 0.2);
    padding: 1.875em 0;
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 0.625em 1em;
    p {
      font-size: 0.75em;
      line-height: 115%;
    }
    p a {
      &:hover {
        color: #36317B;
      }
    }
  }
  &__img {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    max-width: 56.125em;
    width: 100%;
    padding: 0 1.5em;
    img {
      position: relative;
      z-index: 1;
      max-width: 100%;
      max-height: 100%;
    }
  }
  .circle {
    position: absolute;
    left: 5em;
    bottom: -3.75em;
    width: 7.25em;
    height: 7.25em;
    background: #0BAE5F;
    filter: blur(6.25em);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    left: 40%;
    bottom: -3.125em;
    transform: translate(-50%, 0);
    width: 7.25em;
    height: 7.25em;
    background: #060794;
    filter: blur(6.25em);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    right: 5em;
    bottom: -3.75em;
    width: 7.25em;
    height: 7.25em;
    background: #0BAE5F;
    filter: blur(6.25em);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle4 {
    position: absolute;
    right: 40%;
    bottom: -3.125em;
    transform: translate(50%, 0);
    width: 7.25em;
    height: 7.25em;
    background: #060794;
    filter: blur(6.25em);
    border-radius: 50%;
    pointer-events: none;
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    &__group {
      gap: 2em;
    }
    &__group2 {
      gap: 2em;
    }
    &__logo {
      width: 7.5em;
    }
    &__list {
      li {
        font-size: 0.813em;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    &__top {
      flex-direction: column;
      gap: 24px;
    }
    &__group {
      flex-direction: column;
      gap: 24px;
    }
    &__group2 {
      flex-direction: column;
      gap: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__top {
      align-items: start;
      padding: 1.5em 0;
    }
    &__group {
      width: 100%;
      align-items: start;
      border-bottom: 0.063em solid rgba(255, 255, 255, 0.2);
      padding-bottom: 1.5em;
    }
    &__logo {
      width: 7em;
    }
    &__list {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      li {
        font-size: 0.75em;
      }
    }
    &__bottom {
      align-items: start;
      padding: 1.5em 0;
    }
    &__group2 {
      width: 100%;
      align-items: start;
    }
    &__btn {
      width: 100%;
    }
  }
}