.header {
  position: relative;
  z-index: 10;
  background: #fff;
  padding: 1.125em 0;
  &:after {
    content: '';
    position: absolute;
    z-index: 4;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.063em;
    background: rgba(60, 62, 71, 0.15);
    pointer-events: none;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2em;
  }
  &__logo {
    position: relative;
    z-index: 4;
    width: 8.625em;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 2em;
    li a {
      font-size: 0.875em;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
      &:hover {
        color: #36317B;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 0.625em;
  }
  &__btn {
    height: 2.5em;
    padding: 0.313em 1.25em;
    span {
      font-size: 0.875em;
    }
  }
  &__account-wrap {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 0.5em;
  }
  &__account {
    display: flex;
    align-items: center;
    gap: 0.5em;
    color: #36317B;
    i {
      width: 1.125em;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 0.875em;
      line-height: 115%;
      font-weight: 500;
    }
  }
  &__account-exit {
    display: flex;
    align-items: center;
    width: 0.938em;
    img {
      width: 100%;
    }
  }
  &__mob {
    position: relative;
    z-index: 3;
    display: none;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
  }
}
.burger {
  position: relative;
  z-index: 10;
  width: 1.25em;
  height: 1.125em;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.125em;
    background: #36317B;
    border-radius: 0.188em;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.5em;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5em;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 0;
    &__wrap {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      text-align: center;
      gap: 5em;
      background: #fff;
      padding: 7em 0.938em 2.5em 0.938em;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .header__logo {
        display: none;
      }
    }
    &__list {
      width: 100%;
      flex-direction: column;
      gap: 5em;
      li {
        position: relative;
        width: 100%;
        &:not(:last-child) {
          &:after {
            content: '';
            position: absolute;
            left: 50%;
            bottom: -2.5em;
            transform: translate(-50%, 0);
            width: calc(100% + 1.875em);
            height: 1px;
            background: rgba(0, 0, 0, 0.1);
          }
        }
      }
    }
    &__mob {
      display: flex;
      padding: 0.938em 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% + 1.875em);
        height: 100%;
        background: #fff;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__wrap {
      padding-top: 6.25em;
    }
    &__logo {
      width: 6.75em;
    }
    &__btns {
      width: 100%;
      flex-direction: column;
    }
    &__btn {
      width: 100%;
    }
  }
}